	module.exports = {"data":{"site":{"siteMetadata":{"title":"aendrew.com v8","author":"Aendrew Rininsland"}},"markdownRemark":{"id":"/Users/andrew.rininsland/Projects/OTHER/aendrew.github.io/src/pages/2013-03-11-podsix-lives/index.md absPath of file >>> MarkdownRemark","html":"<p>After Minehost went belly-up, the communal Minecraft server I played on went dead —and stayed dead — for about 6 months. However, I have revived it, on brand spanking new hardway and several major iterations of Minecraft later! Not only that, but it’s running the bad-assery that is Tekkit Lite, which means you can do crazy things like build automated mining robots, write simple computer programs <strong>in-game</strong>, harness the power of volcanoes, <strong><em>even create whole new dimensions via books like in Myst.</em></strong></p>\n<p>Seriously, I have no idea why it took me so long to get on top of Tekkit. That shiz’s cray. </p>\n<p>Because there’s not much in terms of anti-griefing code on the server right now, it’s invitation-only. Please contact me either via <a href=\"http://aendrew.com/contact\">email</a>, <a href=\"http://twitter.com/aendrew\">Twitter</a>, Facebook, etc. if you want the address.</p>","frontmatter":{"title":"PodSix lives!","date":"March 11, 2013"}}},"pathContext":{"path":"content/podsix-lives"}}

